import * as React from "react";
import { Link } from "gatsby";
import { makeStyles } from "@material-ui/core/styles";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";
import {
  createMuiTheme,
  responsiveFontSizes,
  ThemeProvider,
} from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import MaterialTable from "@material-table/core";
import { tableIcons } from "../tableicons";
import { hospitalDatas } from "../hospital-data";

export const ResponsiveTypography = (props) => {
  return <Typography variant={props.variant}>{props.children}</Typography>;
};
const useStyles = makeStyles({
  table: {
    minWidth: 650,
  },
});

function createData(
  hospitalName,
  address,
  reserveType,
  reserveStart,
  vaccineStart,
  reserveWay,
  homePageUrl,
  tel
) {
  return {
    hospitalName,
    address,
    reserveType,
    reserveStart,
    vaccineStart,
    reserveWay,
    homePageUrl,
    tel,
  };
}

const headers = [
  "",
  "",
  "",
  "",
  "",
  "",
  "HP",
  "TEL",
];

const columns = [
  { title: "", field: "hospitalName" },
  {
    title: "",
    field: "address",
    render: (row) => <a href={row.address.url}>{row.address.name}</a>,
    customFilterAndSearch: (term, row) => row.address.name.indexOf(term) > -1,
  },
  { title: "( or )", field: "reserveType" },
  { title: "", field: "reserveWay" },
  {
    title: "",
    field: "reserveStart",
    filtering: false,
  },
  { title: "", field: "vaccineStart", filtering: false },
  {
    title: "HP",
    field: "homePageUrl",
    render: (row) => <a href={row.homePageUrl}>HP</a>,
    filtering: false,
  },
  { title: "TEL", field: "tel", filtering: false },
];

const FilterableTable = ({ title, columns, data, ...props }) => {
  return (
    <MaterialTable
      icons={tableIcons}
      title={title}
      columns={columns}
      data={data}
      {...props}
    />
  );
};
const BasicTable = () => {
  const classes = useStyles();

  return (
    <TableContainer component={Paper}>
      <Table className={classes.table} aria-label="simple table">
        <TableHead>
          <TableRow>
            {headers.map((header) => (
              <TableCell>{header}</TableCell>
            ))}
          </TableRow>
        </TableHead>
        <TableBody>
          {hospitalDatas.map((row) => (
            <TableRow key={row.hospitalName}>
              <TableCell component="th" scope="row">
                {row.hospitalName}
              </TableCell>
              <TableCell>
                <a href={row.address.url}>{row.address.name}</a>
              </TableCell>
              <TableCell>{row.reserveType}</TableCell>
              <TableCell>{row.reserveStart}</TableCell>
              <TableCell>{row.vaccineStart}</TableCell>
              <TableCell>{row.reserveWay}</TableCell>
              <TableCell>
                <a href={row.homePageUrl}>HP</a>
              </TableCell>
              <TableCell>{row.tel}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
};

const Top = () => {
  let theme = createMuiTheme();
  theme = responsiveFontSizes(theme);
  return (
    <>
      <ThemeProvider theme={theme}>
        <ResponsiveTypography variant="h4">
          
        </ResponsiveTypography>
        <ResponsiveTypography variant="body1"></ResponsiveTypography>
        <ResponsiveTypography variant="body2">
          
        </ResponsiveTypography>
        <ResponsiveTypography variant="body2">
          
        </ResponsiveTypography>
        <ResponsiveTypography variant="body2">
          
          <a href="https://form.kintoneapp.com/public/form/show/80d93ad98127e9285c0a9d0d6dcbfbfc8aabf0f7c77e64e66f3487b14e40ce68">
            WEB
          </a>
          
        </ResponsiveTypography>
        <ResponsiveTypography variant="body2">
          (0570-022-160)
        </ResponsiveTypography>
        <ResponsiveTypography variant="body2">
          <Link to="/about"></Link>
        </ResponsiveTypography>
        <ResponsiveTypography variant="body2">
          <p>
            :
            <a href="https://www.city.kakogawa.lg.jp/corona/31856.html">
              |
            </a>
          </p>
        </ResponsiveTypography>
      </ThemeProvider>
      {/* <BasicTable /> */}
      <FilterableTable
        title={""}
        columns={columns}
        data={hospitalDatas}
        options={{ filtering: true, pageSize: 10 }}
      />
    </>
  );
};

export default Top;
